.i {
    display: flex;
    height: 100vh;
}

.i-name {
    font-size: 60px;
}

.i-right {
    flex: 1;
    position: relative;
}

.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.i-left-wrapper {
    padding: 50px;
    height: 65vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro {
    font-style: 30px;
    font-weight: 300;

}

.i-name {
    font-style: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
}

.i-title-wrapper::-webkit-scrollbar {
    display: none;
}

.i-title-item {
    animation: move 10s ease-in-out infinite alternate;
}


@keyframes move {
    25% {
        transform: translateY(-50px);
    }

    50% {
        transform: translateY(-100px);
    }

    75% {
        transform: translateY(-150px);
    }

    100% {
        transform: translateY(-200px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 25px;
    font-style: 30px;
    font-weight: bold;
    color: var(--sub-font-color);
    display: flex;
    align-items: center;
}

.i-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 0px;
}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    position: absolute;
    filter: grayscale();
}

.i-bg {
    border-radius: 50%;
    width: 100%;
    padding-top: 100%;
    position: absolute;
    bottom: 0;
    right: 0;
}

.outrun-sun {
    background-image: url(../../images/outrun-sun.png);
    background-repeat: no-repeat;
    background-size: cover;
    transition: all 1s ease;

}

.vapour-sun {
    background-color: var(--sub-font-color);
    transition: all 1s ease;

}

@media screen and (max-width: 984px) {
    .i {
        flex-direction: column;
    }

    .i-left-wrapper {

        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }

    .i-intro {
        font-size: 15px;
    }

    .i-name {
        font-size: 35px;
    }

    .i-title-item {
        display: block;
        font-size: 20px;
    }

    .i-desc {
        display: none;
    }

}